
body {
    overflow: hidden;
    margin: 0px;
}

#app-main {
    position: absolute;
    top:0px;
    left: 0px;
    width:100vw;
    height:100vh;
}
#menu-bar {
    position: absolute;
    width:100vw;
    height:60px;
}
#main-container {
    position: absolute;
    top:60px;
    width:100vw;
    height:calc(100vh - 60px);
}
#right-panel {
    position: absolute;
    top:60px;
    left:100vw;
    width:20vw;
    height:calc(100vh - 60px);
}
#left-panel {
    position: absolute;
    top:60px;
    left:-20vw;
    width:20vw;
    height:calc(100vh - 60px);
}
  

#workspace {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width:100%;
    height:100%;
}
.lasso {
    stroke-width: 1px;
    stroke: #3cace7;
    fill: rgba(20, 125, 255, 0.1);
    stroke-dasharray: 10 5;
}
.lasso_hovered {
    stroke: gray;
    fill: rgba(20, 125, 255, 0.2);
    stroke-dasharray: 0 0;
}
.axis path {
  display: none;
}
.axis line {
  stroke-opacity: 0.1;
  shape-rendering: crispEdges;
}
.active {
  stroke: #000;
  stroke-width: 2px;
}
